import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { environment as Constants } from "src/environments/environment";

@Injectable({
  providedIn: 'root'
})
export class FieldsService {

  constructor(private http: HttpClient) { }

  addField(data){
    return this.http.post(Constants.addField,data);
  }
  GetFields(){
   return this.http.get(Constants.GetFields);
  }
  deleteField(data){
    return this.http.post(Constants.deleteField,data);
  }
  updateField(data){
    return this.http.patch(Constants.updateField,data);
  }
}
