import { Component, OnInit } from '@angular/core';
import { NgbModal,NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr'
import { FormBuilder, FormGroup, Validators,FormControl, FormArray} from '@angular/forms';
import { ActivatedRoute, Router } from "@angular/router";
import { FieldsService } from "./../../services/fields.service";

@Component({
  selector: 'app-fields-list',
  templateUrl: './fields-list.component.html',
  styleUrls:  ['./fields-list.component.scss']
})
export class FieldsListComponent implements OnInit {

  currentPage = 1;
  itemsPerPage = 12;
  modal:NgbModalRef;
  buttonDisable:boolean = false;
  forms : FormGroup;
  fields:any;
  activeField:any;

  constructor(
    private modalService: NgbModal,
    private toastr:ToastrService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private service:FieldsService,
  ) { }

  ngOnInit(): void {
    this.buttonDisable = false;
    this.forms = this.formBuilder.group({
                     Name :['',Validators.required],
                  });
    this.GetFields();
  }

  openModal(smallModalContent){
    this.forms.reset();
    this.modal = this.modalService.open( smallModalContent , { size : 'sm' } );
  }
  markFormGroupTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach(control => {
    control.markAsTouched();

    if (control.controls) {
        this.markFormGroupTouched(control);
    }
    });
    return true;
  }

  addField(){
    const forms = this.forms.value;
    this.buttonDisable = true;
    let data = [forms];
      this.service.addField(data).subscribe(response => {
        this.buttonDisable = false;
        this.modal.close();
        this.toastr.success('Field added');
        this.GetFields();
      },err => {
        this.buttonDisable = false;
          //handle errors here
          this.toastr.error(err.error.Message);
      });
  }

  GetFields(){
    this.service.GetFields().subscribe(response => {
      this.fields = response;
    },err => {
        //handle errors here
        this.toastr.error(err.error.Message);
    });

  }
  deleteField(field){
    if(confirm('Are you sure to delete')){
      let data = [field];
        this.service.deleteField(data).subscribe(response => {
          this.toastr.success('Field deleted');
          this.GetFields();
        },err => {
            //handle errors here
            this.toastr.error(err.error.Message);
        });
    }
  }

  editFieldModal(smallModalContent,field){
    this.forms.setValue({Name:field.Name});
    this.activeField = field;
    this.modal = this.modalService.open( smallModalContent , { size : 'sm' } );
  }

  updateField(){
    const forms = this.forms.value;
    forms.Id = this.activeField.Id;
    this.buttonDisable = true;
    let data = [forms];
      this.service.updateField(data).subscribe(response => {
        this.buttonDisable = false;
        this.modal.close();
        this.toastr.success('Field updated');
        this.GetFields();
      },err => {
        this.buttonDisable = false;
          //handle errors here
          this.toastr.error(err.error.Message);
      });
  }

}
